@import url(https://fonts.googleapis.com/css?family=Covered+By+Your+Grace);

.writing-container {
  width: 100%;
  height: 100%;
  position: relative; }
  .writing-container .writing-lines {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0; }
    .writing-container .writing-lines li {
      border-top: 1px dotted #d3d2d2; }
      .writing-container .writing-lines li:last-child {
        border-bottom: 1px dotted #d3d2d2; }
  .writing-container ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%; }
    .writing-container ul li {
      z-index: 99;
      height: 25px;
      width: 100%;
      padding-left: 50px;
      padding-top: 8px; }
      .writing-container ul li:hover {
