.new-item {
  padding: 50px;
  display: flex;
  flex-direction: column; }
  .new-item input[name='name'], .new-item input[name='quantity'] {
    background-color: #f5f5f5;
    border: none;
    outline: none;
    font-size: 16px;
    height: 25px;
    padding: 20px 10px;
    border-radius: 5px;
    color: rgba(0, 0, 0, 0.8);
    margin-top: 10px; }
    .new-item input[name='name']::-webkit-input-placeholder, .new-item input[name='quantity']::-webkit-input-placeholder {
      font-size: 14px; }
    .new-item input[name='name']::-ms-input-placeholder, .new-item input[name='quantity']::-ms-input-placeholder {
      font-size: 14px; }
    .new-item input[name='name']::placeholder, .new-item input[name='quantity']::placeholder {
      font-size: 14px; }
  .new-item input[type='submit'] {
    margin-top: 10px;
    background-color: green;
    color: white;
    border: none;
    outline: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); }

